import React from 'react';

class ChuckJoke extends React.Component {
	constructor() {
		super();
		this.state={
			joke:'',
			message:'Show me a joke!!!'
		}
	}

	linkGenerator = () => {
		fetch('https://api.chucknorris.io/jokes/random')
		.then(response=>response.json())
		.then(data=>{
			this.setState({joke: data.value})
			this.setState({message: "Another!!!"})
		})
	}
	

	render() {
		return (
			<div className="tc">
				<h2 style={{"cursor":"pointer"}} class="tc f-2 link dim ph3 pv2 mb2 dib white bg-dark-green" onClick={()=>this.linkGenerator()}>{this.state.message}!!!</h2>
				<div class="pa4" style={{"widht":"800px"}}>
				  <div class="bg-light-red athelas ml0 mt0 pl4 black-90 bw2 b-yellow">
				    <p class="f-5 lh-copy measure mt0">{`${this.state.joke}`}</p>
				  </div>
				</div>
			</div>
			);
	}

}

export default ChuckJoke;