import React from 'react';

class Tronald extends React.Component {
	constructor() {
		super();
		this.state={
			joke:'',
			message:'What did he say?'
		}
	}

	linkGenerator = () => {
		fetch('https://tronalddump.io/random/meme')
		.then(response=>{
			this.setState({joke: response.url})
			this.setState({message: "Sorry! Can't refresh (ERR Status: 400)"})
		})
	}
	

	render() {
		return (
			<div className="tc">
				<h2 style={{"cursor":"pointer"}} className="tc f-2 link dim ph3 pv2 mb2 dib white bg-dark-green" onClick={()=>this.linkGenerator()}>{this.state.message}</h2>
				<h5 className="white">Please be patient. The image may take some time to load.</h5>
				<div>
					<img src={this.state.joke} alt="meme" style={{"width":"70vw", "height":"auto"}}/>
				</div>
			</div>
			);
	}

}

export default Tronald;