import React from 'react';

class NumberFact extends React.Component {
	constructor() {
		super();
		this.state={
			search:'',
			fact:''
		}
	}

	linkGenerator = () => {
		fetch(`http://www.numbersapi.com/${this.state.search}?json`)
		.then(response=>response.json())
		.then(data=>{
			this.setState({fact:data.text})
		})
	}
	
	handleChange=(event)=> {
		this.setState({search: event.target.value})
		console.log(this.state.search)
	}

	render() {
		return (
			<div className="tc">
				<div className="pa2">
					<input className="pa3 bg-lightest-blue ba b--green w-80" type="search" placeholder="Enter a number" onChange={this.handleChange} />
				</div>
				<p class="f6 link dim ph3 pv2 mb2 dib white bg-dark-green" onClick={()=>this.linkGenerator()}>Let's Go!</p>
				<div class="pa4" style={{"widht":"800px"}}>
				  <div class="bg-light-red athelas ml0 mt0 pl4 black-90 bw2 b-yellow">
				    <p class="f-5 lh-copy measure mt0">{`${this.state.fact}`}</p>
				  </div>
				</div>
			</div>
			);
	}

}

export default NumberFact;