import React from 'react';

class Trivia extends React.Component {
	constructor() {
		super();
		this.state={
			question:'',
			answer:'',
			output:'',
		}
	}

	setQuestion = (query) => {
		fetch(`https://opentdb.com/api.php?amount=1&category=${query}&difficulty=medium&type=boolean`)
		.then(response=>response.json())
		.then(data=>{
			console.log(data.results)
			this.setState({question: data.results[0].question})
			this.setState({answer: data.results[0].correct_answer})
			this.setState({output: "Click to reveal answer"})
		})
	}

	revealAnswer = () => {
		this.setState({output: this.state.answer})
	}
	

	render() {
		return (
			<div className="tc">
				<p className="f-2 white"> All questions are True/False. Please select a category. </p>
				<nav class="ph3 ph5-ns pv4">
				  <div class="nowrap overflow-x-auto">
				    <p class="link dim gray f5 f4-ns dib mr3" style={{"cursor":"pointer"}} onClick={()=>this.setQuestion(9)}>General Knowledge</p>
				    <p class="link dim gray f5 f4-ns dib mr3" style={{"cursor":"pointer"}} onClick={()=>this.setQuestion(30)}>Gadgets and Tech</p>
				    <p class="link dim gray f5 f4-ns dib mr3" style={{"cursor":"pointer"}} onClick={()=>this.setQuestion(20)}>Mythology</p>
				    <p class="link dim gray f5 f4-ns dib mr3" style={{"cursor":"pointer"}} onClick={()=>this.setQuestion(21)}>Sports</p>
				    <p class="link dim gray f5 f4-ns dib mr3" style={{"cursor":"pointer"}} onClick={()=>this.setQuestion(25)}>Art</p>
				    <p class="link dim gray f5 f4-ns dib mr3" style={{"cursor":"pointer"}} onClick={()=>this.setQuestion(23)}>History</p>
				    <p class="link dim gray f5 f4-ns dib mr3" style={{"cursor":"pointer"}} onClick={()=>this.setQuestion(12)}>Music</p>
				    <p class="link dim gray f5 f4-ns dib mr3" style={{"cursor":"pointer"}} onClick={()=>this.setQuestion(11)}>Films</p>
				    <p class="link dim gray f5 f4-ns dib mr3" style={{"cursor":"pointer"}} onClick={()=>this.setQuestion(10)}>Books</p>
				    <p class="link dim gray f5 f4-ns dib mr3" style={{"cursor":"pointer"}} onClick={()=>this.setQuestion(31)}>Anime and Manga</p>
				  </div>
				</nav>
				<div class="pa4" style={{"widht":"800px"}}>
				  <div class="bg-light-red athelas ml0 mt0 pl4 black-90 bw2 b-yellow">
				    <p class="f-5 lh-copy measure mt0">{`${this.state.question}`}</p>
				  </div>
				  <p className="white f5" onClick={()=>this.revealAnswer()} style={{"cursor":"pointer"}}> {this.state.output}</p>
				</div>
			</div>
			);
	}

}

export default Trivia;