import React from 'react';

class DogSnap extends React.Component {
	constructor() {
		super();
		this.state={
			link:'',
			message:'Show me a doggo!!!'
		}
	}

	linkGenerator = () => {
		fetch('https://dog.ceo/api/breed/retriever/golden/images/random')
		.then(response=>response.json())
		.then(data=>{
			this.setState({link: data.message})
			this.setState({message: "Another!!!"})
		})
	}
	

	render() {
		return (
			<div className="tc">
				<h2 style={{"cursor":"pointer"}} class="tc f-2 link dim ph3 pv2 mb2 dib white bg-dark-green" onClick={()=>this.linkGenerator()}>{this.state.message}</h2>
				<h5 className="white">Please be patient. The image may take some time to load.</h5>
				<div>
					<img src={this.state.link} alt="dog" style={{"width":"70vw", "height":"auto"}}/>
				</div>
			</div>
			);
	}

}

export default DogSnap;