import React from 'react';
import Card from './Card';
import "./CardList.css";

const CardList = ( { data, onRouteChange }) => {
	const cardComponent = data.map((user, i) => {
		return <Card onRouteChange={onRouteChange} id={data[i].id} title={data[i].title} desc={data[i].desc} url={data[i].url} />
	});

	return (
		<div className="flexed">
			{cardComponent}
		</div>
	);
}


export default CardList;