import React from 'react';

class CatFact extends React.Component {
	constructor() {
		super();
		this.state={
			fact:'',
			message:'Give me a cool fact!'
		}
	}

	linkGenerator = () => {
		fetch('https://catfact.ninja/fact')
		.then(response=>response.json())
		.then(data=>{
			this.setState({fact: data.fact})
			this.setState({message: "Another!!!"})
		})
	}
	

	render() {
		return (
			<div className="tc">
				<h2 style={{"cursor":"pointer"}} class="tc f-2 link dim ph3 pv2 mb2 dib white bg-dark-green" onClick={()=>this.linkGenerator()}>{this.state.message}!!!</h2>
				<div class="pa4" style={{"widht":"800px"}}>
				  <div class="bg-light-red athelas ml0 mt0 pl4 black-90 bw2 b-yellow">
				    <p class="f-5 lh-copy measure mt0">{`${this.state.fact}`}</p>
				  </div>
				</div>
			</div>
			);
	}

}

export default CatFact;