import React from 'react';

class CatSnap extends React.Component {
	constructor() {
		super();
		this.state={
			link:'',
			message:'Show me a cat!!!'
		}
	}

	linkGenerator = () => {
      fetch("https://aws.random.cat/meow")
        .then(response=>response.json())
        .then(link => {
          this.setState({link: link.file});
          this.setState({message: 'Another!!!'})
      })
    }
	
	render() {
		return (
			<div className="tc">
				<h2 style={{"cursor":"pointer"}} class="tc f-2 link dim ph3 pv2 mb2 dib white bg-dark-green" onClick={this.linkGenerator}>{this.state.message}</h2>
				<h5 className="white">Please be patient. The image may take some time to load.</h5>
				<div>
					<img src={this.state.link} alt="cat" style={{"width":"70vw", "height":"auto"}}/>
				</div>
			</div>
			);
	}

}

export default CatSnap;