import React from 'react';
import Tilt from 'react-tilt';
import "./Logo.css"

class Logo extends React.Component {
	render() {
		const {onRouteChange} = this.props
		return (
			<div className="logo-div">
				<Tilt className="Tilt" options={{ max : 45 }} style={{ height: 250, width: 250 }} >
	 				<div className="fl w-100 pa2"> 
	 					<img onClick = {()=>onRouteChange('home')} alt="Logo" className="imageBG" src="https://www.freelogodesign.org/file/app/client/thumb/a162d1df-d83f-41a5-aeea-1a212a0bf5ab_200x200.png?1587988125211" />
	 				</div>
				</Tilt>
			</div>
			);
	}

}

export default Logo;